




















import { Component, Vue, Prop } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src
import Page from '@/components/common-components/page.vue'
import { getFleetList } from '@/store/modules/bus/actions'
import XLSX from 'xlsx'
@Component({
  components: {
    HelloWorld,
    Page
  }
})
export default class carInfo extends Vue {
  $createDialog : any
  $createToast : any
  carList = []
  // 
  page = 1
  limit = 10
  count = 0
  carId : any = ''
  carNums = 0
  carInfo : any = {}
  aoa = [
    ['', '', '', ''],
    ['', '', 18, new Date()],
    ['', '', 22, new Date()]
  ]
  activated () {
    this.carId = this.$route.query.carId
    this.getCarInfo({})
  }
  deactivated () {
    this.carList = []
    this.page = 1
    this.count = 0
    this.carInfo = {}
  }

  @Prop({ default: 'default value' }) readonly propB!: string

  // 
  get showMore () {
    return Math.ceil(this.count / this.limit) >= (this.page + 1)
  }
  get fleetId () {
    return this.$route.query.fleetId
  }
  getCarInfo (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    let carId = this.carId
    const toast = this['$createToast']({
      time: 0,
      txt: '...'
    })
    toast.show()
    this.$store.dispatch('getCarInfo', { carId: carId }).then((response) => {
      if (response.success) {
        this.carNums = response.data.nums
        this.carInfo = response.data.carInfo
      }
      toast.hide()
    })
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
    this.getCarInfo({ skip: skip })
  }
  goTo (type : any) {
    this.$router.push({ path: '/' + type, query: { carId: this.carId, fleetId: this.fleetId, carInfo: this.carInfo } })
  }
  sendSMS () {
    console.log(this.carInfo)
    let sendTime = this.carInfo.departureTime.split(' ')[0]
    let licensePlate = this.carInfo.licensePlate
    if (!licensePlate) {
      const toast = this['$createToast']({
        time: 2000,
        txt: '',
        type: 'none'
      })
      toast.show()
      return
    }
    let content = '' + sendTime + '' + licensePlate + '<p style="color:red"></p>'
    let dialog = this.$createDialog({
      type: 'confirm',
      title: '',
      content: content,
      confirmBtn: {
        text: '',
        active: true,
        disabled: false,
        href: 'javascript:;'
      },
      cancelBtn: {
        text: '',
        active: false,
        disabled: false,
        href: 'javascript:;'
      },
      onConfirm: () => {
        const toast = this['$createToast']({
          txt: '...',
          type: 'none'
        })
        toast.show()
        this.$store.dispatch('sendSMS', { carInfo: this.carInfo }).then((response) => {
          if (response.success) {
            toast.hide()
            const toastSuc = this['$createToast']({
              txt: '',
              type: 'none',
              time: 3000
            })
            toastSuc.show()
          } else {
            const toast = this['$createToast']({
              txt: '',
              type: 'none',
              time: 3000
            })
            toast.show()
          }
        })
      },
      onCancel: () => {}
    })
    dialog.show()
  }
  exportOrder () {
    this.$store.dispatch('getCarAllOrderList', { carInfo: this.carInfo }).then((response) => {
      if (response.success) {
        let excelList = []
        for (let k = 0; k < response.data.length; k++) {
          let jsonArr = this.jsonToArr(response.data[k])
          excelList.push(jsonArr)
        }
        const ws = XLSX.utils.aoa_to_sheet(excelList)
        const wb = XLSX.utils.book_new()
        XLSX.utils.book_append_sheet(wb, ws, 'SheetJS')
        let excelName = '' + this.carInfo.licensePlate
        XLSX.writeFile(wb, excelName + '.xlsx')
      } else {}
    })
  }
  jsonToArr (obj : any) {
    let arr = []
    for (let k in obj) {
      if (k === 'start') {
        arr.push(obj[k])
      } else if (k === 'end') {
        arr.push(obj[k])
      } else if (k === 'name') {
        arr.push(obj[k])
      } else if (k === 'phone') {
        arr.push(obj[k])
      }
    }
    return arr
  }
}
